___CSS_LOADER_EXPORT___.push([module.id, `.todo-items {
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
}

.item {
    margin: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

.card {
    padding: 1rem 2rem;
    width: 70%;
    border: 1px solid #ff8c00;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 1rem;
    transition: 250ms linear;
}

.card > * {
    margin: 0px;
}

.card:hover {
    scale: 1.02;
}

.card-title {
    font-family: '3dumb';
    font-size: 1.5rem;
    font-weight: bold;
}

.card-duedate {
    font-style: italic;
    font-weight: bold;
    font-size: 1rem;
}

.card-project {
    font-family: '3dumb';
    font-weight: bold;
    font-size: 1.5rem;
}

.high-priority {
    background-color: rgb(231, 94, 151);
}

.medium-priority {
    background-color: #b07df3;
}

.low-priority {
    background-color: rgb(129, 216, 129);
}

.card-circle-unchecked,
.card-circle-checked {
    width: 30px;
    height: 30px;
}

.card-delete {
    width: 4%;
    height: 4%;
}

.card-delete:hover {
    cursor: pointer;
    scale: 1.1;
}

.card-delete:active {
    scale: 1;
}

.card-circle-checked.card-checked {
    display: inline;
}

.item.item-checked > .card {
    text-decoration: line-through;
    opacity: 0.5;
}`, "",{"version":3,"sources":["webpack://./src/css/todoItem.css"],"names":[],"mappings":"AAAA;IACI,YAAY;IACZ,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,yBAAyB;IACzB,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,mBAAmB;IACnB,wBAAwB;AAC5B;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,oBAAoB;IACpB,iBAAiB;IACjB,iBAAiB;AACrB;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,oBAAoB;IACpB,iBAAiB;IACjB,iBAAiB;AACrB;;AAEA;IACI,mCAAmC;AACvC;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,oCAAoC;AACxC;;AAEA;;IAEI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,SAAS;IACT,UAAU;AACd;;AAEA;IACI,eAAe;IACf,UAAU;AACd;;AAEA;IACI,QAAQ;AACZ;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,6BAA6B;IAC7B,YAAY;AAChB","sourcesContent":[".todo-items {\n    height: 100%;\n    overflow-y: auto;\n    overflow-x: hidden;\n}\n\n.item {\n    margin: 2rem;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    gap: 1rem;\n}\n\n.card {\n    padding: 1rem 2rem;\n    width: 70%;\n    border: 1px solid #ff8c00;\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    border-radius: 1rem;\n    transition: 250ms linear;\n}\n\n.card > * {\n    margin: 0px;\n}\n\n.card:hover {\n    scale: 1.02;\n}\n\n.card-title {\n    font-family: '3dumb';\n    font-size: 1.5rem;\n    font-weight: bold;\n}\n\n.card-duedate {\n    font-style: italic;\n    font-weight: bold;\n    font-size: 1rem;\n}\n\n.card-project {\n    font-family: '3dumb';\n    font-weight: bold;\n    font-size: 1.5rem;\n}\n\n.high-priority {\n    background-color: rgb(231, 94, 151);\n}\n\n.medium-priority {\n    background-color: #b07df3;\n}\n\n.low-priority {\n    background-color: rgb(129, 216, 129);\n}\n\n.card-circle-unchecked,\n.card-circle-checked {\n    width: 30px;\n    height: 30px;\n}\n\n.card-delete {\n    width: 4%;\n    height: 4%;\n}\n\n.card-delete:hover {\n    cursor: pointer;\n    scale: 1.1;\n}\n\n.card-delete:active {\n    scale: 1;